n  --normal-text: 16px;\n  --small-text: 14px;\n  --large-text: 36px;\n}\n\nbody {\n  font-family: \"Roboto\", sans-serif;\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\n/* navbar content*/\nheader nav {\n  padding: 0 8%;\n  position: fixed;\n  z-index: 2;\n  top: 0;\n  right: 0;\n  left: 0;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background: white;\n  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;\n  color: #333;\n}\n\n.nav-list {\n  display: flex;\n}\n\nnav .logo a {\n  min-width: 48px;\n  min-height: 48px;\n  padding: 13px; \n}\n\nnav .nav-list a {\n  text-decoration: none;\n  color: var(--font-color);\n  padding: 0 20px;\n  min-width: 44px;\n  min-height: 44px;\n  font-weight: 500;\n  font-size: var( --normal-text);\n  display: inline-block;\n  line-height: 44px;\n}\n\nnav .nav-list a:hover {\n  color: var(--primary-color);\n}\n\nnav .nav-list li {\n  list-style: none;\n}\n\n/* skip to content */\n.skip-link {\n  position: absolute;\n  top: -60px;\n  left: 10px;\n  color: var(--primary-color);\n  color: #fff;\n  padding: 20px 30px;\n  z-index: 200;\n  transition: all 0.3s ease-in-out;\n  text-decoration: none;\n}\n\n.skip-link:focus {\n  top: 10px;\n}\n\n/* hero content*/\n.hero .btn a {\n  text-decoration: none;\n  padding: 18px 40px;\n  font-size: 16px;\n  font-weight: 600;\n  background-color:  #F86060;\n  border-radius: 14px;\n  color: #fff;\n}\n\n.hero .btn a:hover {\n  background-color: var(--hover-color);\n  transition: all 0.3s ease-in;\n} \n\n.hero .btn {\n  margin-top: 48px;\n}\n\n/* main */\n.explore-restaurant {\n  margin-top: 100px;\n  padding: 0 8%;\n}\n\n.text-head h2 {\n  font-size: 40px;\n}\n\n.text-head {\n  text-align: center;\n}\n\n.text-head h1{\n  margin-top: 12px;\n  font-size: var(--large-text);\n}\n\n.restaurant {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  column-gap: 2%;\n  row-gap: 24px;\n  justify-content: center;\n  margin-top: 52px !important;\n  padding: 0 8%;\n}\n\n.content {\n  margin-top: 100px;\n}\n\n.restaurant-description .card-des {\n  margin-top: 16px;\n  font-size: 16px;\n  line-height: 1.5em;\n  text-align: justify;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  -webkit-box-orient: vertical;\n}\n\n.restaurant .restaurant-item:hover {\n  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;\n  transition: all 0.1s ease-in;\n}\n\n.restaurant .restaurant-item a {\n  padding: 10px 0;\n}\n\n.restaurant .restaurant-item .skeleton {\n  user-select: none;\n  background-color: #eeeeee;\n  color: #eeeeee;\n  overflow: hidden;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 4;\n}\n\n.restaurant .restaurant-item{\n  box-shadow: var(--card-shadow);\n  border-radius: 12px;\n}\n\n.restaurant .restaurant-item img {\n  width: 100%;\n  height: 200px;\n  border-radius: 12px 12px 0 0;\n  object-fit: cover;\n}\n\n.restaurant .restaurant-item .ratting {\n  display: flex;\n  align-items: center;\n}\n\n.restaurant .restaurant-item a{\n  text-decoration: none;\n  color: var(--font-color);\n}\n\n.restaurant .restaurant-item .ratting p {\n  margin-left: 4px;\n}\n\n.restaurant .restaurant-item .heading {\n  display: flex;\n  justify-content: space-between;\n}\n\n.restaurant .restaurant-item .card-body {\n  padding:  0 16px 0px 16px;\n}\n\n.location {\n  display: flex;\n  align-items: center;\n  margin-top: -30px;\n}\n\n.restaurant .restaurant-item .location p {\n  margin-left: 4px;\n  color: var(--font-color);\n}\n\n.restaurant .restaurant-item .card-body .restaurant-description p{\n  color: var(--font-color);\n  line-height: 22px;\n  margin-top: -1px; \n}\n\n/* testimonials content*/\n.section-testimonials {\n  background-color: var(--secondary-color);\n  margin-top: 140px;\n  padding: 64px 8%;\n}\n\n.section-testimonials .text-head {\n  text-align: center;\n}\n\n.testimonial-content .card-testimonial {\n  width: 320px;\n  padding: 20px;\n  background: white;\n  border-radius: 16px;\n  margin-top: 32px;\n}\n\n.card-testimonial .text-card p{\n  line-height: 24px;\n}\n\n.testimonial-content .card-testimonial .profile {\n  display: flex;\n  align-items: center;\n  gap: 0 16px;\n}\n\n.testimonial-content .card-testimonial .profile h4 {\n  font-weight: 600;\n}\n\n.card-testimonial .profile p {\n  font-size: var(--small-text);\n  margin-top: -16px;\n  color: var(--font-color);\n}\n\n.card-testimonial:hover {\n  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;\n  transition: all 0.3s ease-in;\n}\n\n/* qna content*/\n.qna-section {\n  margin-top: 140px;\n  padding: 0 8%\n}\n\n.qna-section .text-head {\n  text-align: center;\n}\n\n.qna-section .main-content {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  max-width: 800px; /* Lebar maksimum 500px */\n  margin: 32px auto 0 auto; /* Menengahkan elemen */\n}\n\n.content-item:hover {\n  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px !important;\n  transition: all 0.1s ease-in;\n}\n\n.content-item {\n  margin-top: 24px;\n}\n\n.content-item.active svg{\n  transform: rotate(180deg);\n}\n\n.content-item.active .answer {\n  max-height: 300px;\n}\n\n.qna-section .question {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.qna-section .answer p {\n  font-size: var(--normal-text);\n  line-height: 1.6;\n  margin-top: 0;\n}\n\n.qna-section .answer {\n  max-height: 0;\n  overflow: hidden;\n  transition: max-height 0.4s ease;\n}\n\n.qna-section .content-item {\n  box-shadow: var(--card-shadow);\n  border-radius: 20px;\n  padding: 0px 20px;\n  cursor: pointer;\n}\n\n\n/* footer content*/\nfooter .row{\n  display: flex;\n  justify-content: space-between;\n  padding: 54px 8%;\n  flex-wrap: wrap;\n}\n\nfooter {\n  background-color: var(--secondary-color);\n  margin-top: 140px;\n}\n\nfooter li {\n  list-style: none;\n}\n\nfoooter p {\n  color: var(--font-color);\n}\n\nfooter li a {\n  color: var(--font-color);\n  text-decoration: none;\n  display: inline-block;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 4px;\n  text-align: center;\n}\n\nfooter .img-link a {\n  display: inline-block;\n  width: 44px;\n  height: 44px;\n  padding: 4px 4px 0 4px;\n  text-align: center;\n}\n\nfooter li a:hover{\n  color: var(--primary-color);\n}\n\nfooter ul {\n  padding-top: 12px;\n  margin-left: -44px;\n}\n\n.footer-col h3 {\n  margin-top: -1px;\n}\n\nfooter .container {\n  padding-bottom: 32px;\n}\n\nfooter .copyright {\n  text-align: center;\n  font-size: var(--normal-text);\n}\n\n/* detail page content */\n.detail-container {\n  padding: 100px 8% 0 8%;\n}\n\n.detail-content-text .location p {\n  margin-left: 4px;\n  font-size: var(--normal-text);\n}\n\n.detail-page{\n  width: 100%;\n  margin-bottom: 24px;\n}\n\n.img-content img {\n  width: 100%;\n  border-radius: 24px;\n  object-fit: contain;\n  height: 100%;\n}\n\n.descrip p {\n  line-height: 2;\n  font-size: 16px;\n  color: #6D6D6D;\n}\n\n.detail-page h1 {\n  font-size: 34px;\n}\n\n.detail-page .skeleton {\n  user-select: none;\n  background-color: #eeeeee;\n  color: #eeeeee;\n}\n\n.detail-content .like {\n  cursor: pointer;\n  outline: none;\n  border: none;\n  background-color: transparent;\n}\n\n.header-detail {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 24px;\n}\n\n.breadcrumbs p {\n  font-size: var(--normal-text);\n}\n\n.breadcrumbs span {\n  color: #ddd;\n}\n\n.breadcrumbs a{\n  color: #080C2E;\n  text-decoration: none;\n  display: inline-block;\n  width: 44px;\n  height: 44px;\n  text-align: center;\n  line-height: 44px;\n}\n\n.img-content .like_button {\n  display: flex;\n  justify-content: right;\n  align-items: right;\n}\n\n.img-content .like:focus i {\n  border: 2px solid red;\n}\n\n.red {\n  color: red;\n}\n\n.like i {\n  font-size: 20px;\n  font-weight: bold;\n  background: #F2F2F2;\n  height: 45px;\n  width: 45px;\n  border-radius: 50%;\n  margin-top: 5px;\n  justify-content: center;\n  align-items: center;\n  display: flex;\n}\n\n/* review section content*/\n.review-container {\n  margin-top: 54px;\n}\n\n.card_review {\n  display: grid;\n  gap: 16px;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n}\n\n.card_review .review_item:hover {\n  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;\n  transition: all 0.2s ease-in-out;\n}\n\n.card_review .review_item{\n  box-shadow: rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;\n  padding: 8px 16px;\n  border-radius: 14px;\n}\n\n.reviewer_item {\n  margin-bottom: 24px;\n}\n\n.card_review .review_item h4 {\n  font-size: 18px;\n}\n\n.card_review .review_item p {\n  font-size: var(--normal-text);\n}\n\n/* add review */\n.fa-solid {\n  display: none;\n}\n\n.add_container {\n  display: flex;\n  flex-direction: column;\n  margin-top: 54px;\n}\n\n.add_container h2 {\n  font-size: 32px;\n}\n\n.add_container .inputName,textarea {\n  margin-bottom: 16px;\n  padding: 16px;\n  font-family: \"Roboto\", sans-serif;\n  font-size: var(--normal-text);\n  outline: none;\n  border: 1px solid #ddd;\n  border-radius: 12px;\n}\n\n.add_container .inputName:focus, textarea:focus {\n    border: 2px solid #F86060 !important;\n    box-shadow: 0 0 5px rgba(248, 96, 96, 0.5); \n}\n\n.add_container label {\n  margin-bottom: 8px;\n  font-weight: 500;\n  font-size: var(--normal-text);\n}\n\n.add_container .submit button {\n  background-color: var(--primary-color);\n  color: #fff;\n  cursor: pointer;\n  outline: none;\n  border: none;\n  font-size: 16px;\n  border-radius: 14px;\n  padding: 16px;\n  width: 100%;\n}\n\n.categories-wrapper, .foods-wrapper, .drinks-wrapper {\n  display: flex;\n  gap: 8px;\n  padding-top: 8px;\n  flex-wrap: wrap;\n}\n\n.food, .drink, .categorie {\n  cursor: pointer;\n  font-size: 14px;\n  font-weight:400;\n  text-align: justify;\n  padding: 5px 10px;\n  border: 1px solid #f8fafc;\n  background-color: #f1f5f9;\n  border-radius: 5px;\n}\n\n/* favorite section content*/\n.content-favorite h2{\n  font-size: 34px;\n}\n\n.content-favorite .content {\n  padding: 0 8%;\n}\n\n.content-favorite .header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding-bottom: 24px;\n  border-bottom: 1px solid #eee;\n}\n\n.search-container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 4px;\n  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;\n  border-radius: 60px;\n  padding: 8px 10px 8px 16px;\n}\n\n.search-container input {\n  outline: none;\n  border: none;\n  padding: 13px;\n  font-size: var(--normal-text);\n  width: 300px;\n}\n\n.search-container #searchIcon {\n  padding: 12px 14px;\n  cursor: pointer;\n  color: #fff;\n  background-color: var(--primary-color);\n  border-radius: 50px;\n}\n\n/* loader content*/\n\n.loading {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: #f7f9fb;\n  transition: opacity 0.75s, visibility 0.75s;\n  z-index: 999;\n  pointer-events: none;\n}\n\n.loading-hidden {\n  opacity: 0;\n  visibility: hidden;\n}\n\n.loading::after {\n  content: \"\";\n  width: 75px;\n  height: 75px;\n  border: 15px solid #dddddd;\n  border-top-color: var(--primary-color);\n  border-radius: 50%;\n  animation: loading 0.75s ease infinite;\n}\n\n@keyframes loading {\n  from {\n    transform: rotate(0deg);\n  }\n\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* section service content*/\n.our-services {\n  margin-top: 140px;\n  padding: 0 8%;\n}\n\n.container-service {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));\n  gap: 20px;\n  margin-top: 34px;\n}\n\n.card-service:hover {\n  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;\n}\n\n.card-service {\n  padding:24px 24px 8px 24px;\n  box-shadow: var(--card-shadow);\n  border-radius: 16px;\n}\n\n.card-service p{\n  font-size: 16px;\n  color: #5D5D7C;\n  line-height: 1.5;\n}\n.card-service h3{\n  font-size: 22px;\n  color: var(--font-color);\n}"],"sourceRoot":""}]);
